.popup__container {
  position: relative;
  display: grid;
  width: 430px;
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  z-index: 2;
  cursor: default;
}

@media screen and (max-width: 640px) {
  .popup__container {
    width: 282px;
  }
}
